import React from 'react'
import {connect} from 'react-redux'
import {Route, Redirect} from 'react-router-dom'
import {NavBar} from 'antd-mobile'
import NavLinkBar from '../navlinkbar/navlinkbar'
import Boss from '../../component/boss/boss'
import Genius from '../../component/genius/genius'
import User from '../../component/user/user'
import Msg from '../../component/msg/msg'
import {getMsgList, recvMsg, listenerSet} from '../../redux/chat.redux'
import QueueAnim from 'rc-queue-anim'

@connect(
	state=>state,
	{getMsgList, recvMsg, listenerSet}
)
class Dashboard extends React.Component{
	componentDidMount(){
		if(!this.props.chat.listenerset){
			this.props.getMsgList()
			this.props.recvMsg()
			this.props.listenerSet()
		}
	}
	render(){
		const {pathname} = this.props.location
		const user = this.props.user
		const navList = [
			{
				path:'/boss',
				text:'牛人',
				icon:'boss',
				title:'牛人列表',
				component: Boss,
				hide: user.type == 'genius'
			},
			{
				path:'/genius',
				text:'boss',
				icon:'job',
				title:'BOSS列表',
				component: Genius,
				hide: user.type == 'boss'
			},
			{
				path:'/msg',
				text:'消息',
				icon:'msg',
				title:'消息列表',
				component: Msg,
			},
			{
				path:'/me',
				text:'我',
				icon:'user',
				title:'个人中心',
				component: User,
			}
		]

		const page = navList.find(v=>v.path == pathname)

		return page?(
			<div>
				<NavBar className='fixed-header' mode='dark'>{page.title}</NavBar>
				<div style={{marginTop:45, marginBottom: 50}}>
				<QueueAnim type='scaleX'>
						<Route key={page.path} path={page.path} component={page.component}/>
				</QueueAnim>
				</div>
				<NavLinkBar data={navList}></NavLinkBar>
			</div>
		):<Redirect to='/me'></Redirect>
	}
}

export default Dashboard


